import React from "react";
import { withTranslation } from "react-i18next";
import { inject, observer } from "mobx-react";
import { Icon, Progress, message } from "antd";
import QRCode from "qrcode.react";
import "./style.scss";
import { byteConvert, getFile } from "../../util/util";
import Footer from "../../common/Widget/Footer";
import Nodata from "../../common/Widget/Nodata";
import Overview from "./Overview";
import event from "../../util/event";
import { qrArr } from "../../util/url";
import framelogo from "../../images/framelogo.png";
import banner from "../../images/banner.png";
import i18next from "i18next";
import { color } from "../../progresscolor";
import list from "../../images/list_icon.png";
import frameimg from "../../images/frames_icon.png";
import albumimg from "../../images/albums_icon.png";

@withTranslation()
@inject("login", "rahmen")
@observer
class Home extends React.Component {
  componentDidMount() {
    this.props.login.getmain_profile();
    event.on("add_frames", () => {
      this.props.login.addFrames();
    });
    event.on("createplay", () => {
      this.props.login.addplaylist();
    });

    this.wsinit = event.on("wsinit", (res) => {
      if (res === 1) {
        this.props.login.getmain_profile();
      }
    });
  }

  componentWillUnmount() {
    event.rmName("add_frames");
    event.rmName("createplay");
    event.rm(this.wsinit);
  }

  upgradestorage = () => {
    message.warning(i18next.t("Functionnotopen"));
  };

  render() {
    const { t } = this.props;
    const { user, playlist, album, frame, space } = this.props.login;
    const { bindframevisibleshow } = this.props.rahmen;
    return (
      <div className="home-content">
        <div className="home">
          <div className="banner">
            <div className="welcome-box">
              <div className="top-welcome">
                <h2>
                  {t("Welcometouse")}
                  <span>Photo Frame</span>
                </h2>
              </div>
              <div className="middle-list">
                <ul className="clearfix">
                  <li>
                    <span>{t("Multiframemanagement")}</span>
                  </li>
                  <li>
                    <span>{t("Onebuttonsynchronization")}</span>
                  </li>
                  {/* <li><span>{t('Emailisendphotos')}</span></li> */}
                </ul>
              </div>
              <div className="bottom-message">
                <div
                  className={`cloud-size  ${
                    user.space_use > space ? "cloud-size-warning" : ""
                  }`}
                >
                  {user.space_use > space ? (
                    <div className="cloud-size-top">
                      <span className="space">
                        <Icon
                          className="warning"
                          theme="filled"
                          type="warning"
                        />
                        {t("Cloudspace")}
                      </span>
                      {/*<span className='expansion-space' onClick={this.upgradestorage}>{t('Upgradestorage2')}</span>
                       */}
                    </div>
                  ) : (
                    <div className="cloud-size-top">
                      <span className="space">
                        <Icon type="cloud" theme="filled" />
                        {t("Cloudspace")}
                      </span>
                      {/*<span className='expansion-space' onClick={this.upgradestorage}>{t('Upgradestorage')}</span>
                       */}
                    </div>
                  )}
                  <div className="size">
                    <Progress
                      percent={
                        user.space_use
                          ? (user.space_use / user.space_all) * 100
                          : 0
                      }
                      showInfo={false}
                      strokeColor={color}
                    />
                  </div>
                  <div className="warning">
                    <span>
                      {t("Alreadyused")}&nbsp;&nbsp;
                      {user.space_use ? byteConvert(user.space_use) : "0GB"}/
                      {user.space_all ? byteConvert(user.space_all) : "0GB"}
                    </span>
                  </div>
                </div>
                <div className="bindframe-btn">
                  <span onClick={bindframevisibleshow}>
                    {t("BindingFrame")}
                  </span>
                </div>
              </div>
            </div>
            <div className="banner-img">
              <img src={banner} alt="fraems" />
            </div>
          </div>

          <div className="overall-data">
            <Overview
              pushPath={() => {
                this.props.history.push("/playlist");
              }}
              count={playlist.count ? playlist.count : 0}
              title={t("playlist")}
              imglist={playlist.profile}
              img={list}
              color="#D0E2FF"
            />
            <Overview
              pushPath={() => {
                this.props.history.push("/album");
              }}
              count={album.count ? album.count : 0}
              title={t("cloudalbum")}
              imglist={album.profile}
              img={albumimg}
              color="#FFE9DC"
            />
          </div>

          <div className="download-frames">
            <div
              className="overview-inner"
              onClick={() => {
                this.props.history.push("/rahmen");
              }}
            >
              <div className="overview-left">
                <p>
                  <p className="top_icon" style={{ backgroundColor: "#FFDAE7" }}>
                    <span
                      style={{
                        background: `url(${frameimg}) no-repeat center`,
                        backgroundSize: "100% auto",
                      }}
                    ></span>
                  </p>
                </p>
                <p>{frame.count ? frame.count : 0}</p>
                <p>{t("photoframe")}</p>
              </div>
              <div className="home-frame">
                {frame.profile ? (
                  frame.count !== 0 ? (
                    frame.profile.map((item, i) => {
                      return (
                        <div className="frameimgbox" key={i}>
                          <img
                            src={item.id ? getFile.file_s(item.id) : framelogo}
                            alt=""
                          />
                        </div>
                      );
                    })
                  ) : (
                    <Nodata />
                  )
                ) : (
                  <Icon
                    type="loading"
                    style={{ fontSize: 36, color: "#FFDAE7" }}
                    spin
                  />
                )}
              </div>
            </div>

            <div className="download-app">
              <div className="download-app-head">
                <span>{t("DownloadMobileApp")}</span>
              </div>
              <div className="download-app-boby">
                <div className="download-app-explain">
                  <p className="text">{t("appexplain")}</p>
                  <div className="app-qr">
                    {qrArr.map((item, i) => {
                      return (
                        <div className="apple" key={i}>
                          <div className="qr">
                            <QRCode value={item.url} size={100} />
                          </div>
                          <a
                            className="img-icon"
                            href={item.url}
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            <img src={item.img} alt="" />
                          </a>
                        </div>
                      );
                    })}
                  </div>
                </div>
              </div>
            </div>
          </div>

          <Footer />
        </div>
      </div>
    );
  }
}

export default Home;
