import React from "react";
import { Icon } from "antd";
import { getFile } from "../../util/util";

export default class Overview extends React.PureComponent {
  state = {};
  render() {
    const { pushPath, count, title, imglist, img, color } = this.props;
    return (
      <div className="overview-inner" onClick={pushPath}>
        <div className="overview-left">
          <p>
            <p className="top_icon" style={{backgroundColor:color}}>
              <span style={{ background: `url(${img}) no-repeat center`,backgroundSize:'100% auto' }}></span>
            </p>
          </p>
          <p>{count}</p>
          <p>{title}</p>
        </div>
        <div className="overview-right">
          {imglist ? (
            imglist.map((item, i) => {
              return (
                <div
                  className="imgbox"
                  key={i}
                  style={{ backgroundColor: color }}
                >
                  {item.id && <img src={getFile.file_s(item.id)} alt="" />}
                </div>
              );
            })
          ) : (
            <Icon type="loading" style={{ fontSize: 36, color }} spin />
          )}
        </div>
      </div>
    );
  }
}
